/* store.js */
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
state: {
    isShowBar: false
},
getters: {
    getIsShowBar(state){
    return state.isShowBar;
}
},
mutations: {
  
    setIsShowBar(state){
        return state.isShowBar--;
    }
},
actions : {
        setIsShowBar(context) {
            
            return context.commit('setIsShowBar');
        }
    }
});
